import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote
} = app;
let {
	log
} = console;
export default {
	props: ['wares', 'config', 'url'],
	watch:{
		wares(newvalue){
			this.ware=newvalue;
		}
	},
	data() {
		return {
			ware:this.wares,
			imgRemote: imgRemote,
			token: uni.getStorageSync('cdj_token'),
			count:0
		}
	},
	methods: {
		showCart() {
			this.$emit('showCart')
		},
		addcart(url, num, message = '成功加入购物车') {
			let item = this.ware;
			if 	(item.is_float == 1 && !Number.isInteger(Number(num))) {
				rs.Toast( '购买数量不能为小数');
				return;
			}
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				appid: appid,
				timeStamp: timeStamp,
				item_id: item.id,
				attr_id: 0,
				item_num: num
			};
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign({
					sign: sign
				},
				obj
			);
			rs.postRequests(url, params, res => {
				let data = res.data;
				if (data.code == 200) {
					rs.Toast(message)
					if (num <= 0) {
						this.ware.cart_num = 0;
					} else {
						// console.log(this.ware)
						this.ware.cart_num =num;
					}
				} else if (data.code == 407 || data.code == 406) {
					rs.Toast("购买数量不能超过活动数量");
				} else {
					rs.Toast(res.data.msg);
				}
			});
		},
		minus(e) {
			if (e == 0) {
				this.addcart('deleteCart', e, '成功删除商品');
			} else {
				this.addcart('changeNum', e);
			}
		},
		plus(e) {
			// console.log(e)
			this.addcart('changeNum', e);
		},
		plusCart() {
			this.addcart('changeNum', 1);
		},
		showKey() {
			this.$emit('showKey')
		},
		detail() {
			this.count++;
			if (this.count != 1) return;
			setTimeout(() => {
				this.count = 0
			}, 1000)
			if(this.config.is_detail==1){
				uni.navigateTo({
					url: `/pages/index/shopdetail?id=${this.ware.id}`
				})
			}
		},
		cancelCollect(){
			this.$emit('cancelCollect')
		}
	}
}
